import { useState, useEffect } from 'react';
import { PHASE_1_NFT_AMOUNT } from '../constants/misc';
import { useZepochNodeTotalSupply } from './useToken';

export const useCurrentPhase = () => {
  const [phase, setPhase] = useState(0);
  const { data: zepochNodeTotalSupply } = useZepochNodeTotalSupply();

  useEffect(() => {
    if (zepochNodeTotalSupply < PHASE_1_NFT_AMOUNT) {
      setPhase(1);
      return;
    }
    setPhase(2);
  }, [zepochNodeTotalSupply]);

  return phase;
};
