import { useState } from 'react';

import axios from '../utils/axios';

export function usePoints() {
  const [rankList, setTankList] = useState([]);

  const [myRanking, setMyRanking] = useState({});

  function getPointsRanking() {
    axios.get('manage/points/now').then((res) => {
      setTankList(res.data.data?.list || []);
    });
  }

  function getMyRanking(address) {
    axios.get(`manage/points/ranking/${address}`).then((res) => {
      setMyRanking(res.data.data || {});
    });
  }

  return {
    rankList,
    myRanking,
    setTankList,
    getPointsRanking,
    getMyRanking,
  };
}
