import { useEffect, useReducer } from 'react';
import { NFT_TYPE, PHASE_1_LOCK_DURATION, PHASE_1_NFT_AMOUNT } from '../constants/misc';
import { useWallet } from '../store/wallet-context';
import { checkTime, FETCH_STATE, readContractReducer, safeBigNumberToNumber } from '../utils/utils';
import {
  useContract,
  useManagerContract,
  useMulticallContract,
  useThemisGavelContract,
  useZepochNodeContract,
} from './useContract';
// import { use721Allowance } from './useToken';
import { useIsMounted } from './useIsMounted';
import { useUpdate } from './useUpdate';
import { multicallHelper } from '../utils/multicall';
import ZHManagerInterface from '../abis/ZHManagerInterface.json';
import { CONTRACT_NAME } from '../constants/addresses';
import { useNameservice } from '../store/nameservice-context';

const decodePhase1ZbcUserLocked = (record) => ({
  timestamp: safeBigNumberToNumber(record.timestamp),
  unlockable: checkTime((safeBigNumberToNumber(record.timestamp) + PHASE_1_LOCK_DURATION) * 1000),
  unlocked: record.unlocked,
  tokenOwner: record.tokenOwner,
});

export const useNFTRecords = () => {
  const managerContract = useManagerContract();
  const zepochNodeContract = useZepochNodeContract();
  const themisGavelContract = useThemisGavelContract();
  const multicallContract = useMulticallContract();
  const { [CONTRACT_NAME.Manager]: managerAddress } = useNameservice();

  const [state, dispatch] = useReducer(readContractReducer, {
    isLoading: false,
    isError: false,
    data: {},
  });
  const isMounted = useIsMounted();
  const { updateByTimer } = useUpdate();
  const { account } = useWallet();

  useEffect(() => {
    const fetch = async () => {
      try {
        if (
          !(
            managerContract &&
            zepochNodeContract &&
            themisGavelContract &&
            account &&
            managerAddress
          )
        ) {
          return;
        }
        dispatch({ type: FETCH_STATE.INIT });

        const [inWalletZepochNodeIds, themisGavelIds, stakedZepochNodeIds] = await Promise.all([
          zepochNodeContract.tokensOfOwner(account),
          themisGavelContract.tokensOfOwner(account),
          managerContract.stakedZepoch(account),
        ]);

        const convertedStakedZepochNodeIds = stakedZepochNodeIds.map((item) => item.toNumber());
        const zepochNodeIds = inWalletZepochNodeIds
          .concat(stakedZepochNodeIds)
          .map((item) => item.toNumber());

        const phase1ZepochNodeIds = zepochNodeIds.filter(
          (tokenId) => tokenId < PHASE_1_NFT_AMOUNT + 1,
        );
        const phase2ZepochNodeIds = zepochNodeIds.filter(
          (tokenId) => tokenId >= PHASE_1_NFT_AMOUNT + 1,
        );

        const phase1ZbcUserLockedCalls = phase1ZepochNodeIds.map((item) => [
          managerAddress,
          'phase1ZbcUserLocked',
          [item],
        ]);

        const phase1ZbcUserLocked = (
          await multicallHelper(multicallContract, ZHManagerInterface.abi, phase1ZbcUserLockedCalls)
        ).map((item) => decodePhase1ZbcUserLocked(item));

        const phase1ZepochNodeRecords = [];

        phase1ZepochNodeIds.forEach((tokenId, index) => {
          phase1ZepochNodeRecords.push({
            tokenId,
            phase: 1,
            nftType: NFT_TYPE.ZEPOCH,
            phase1ZbcUserLocked: phase1ZbcUserLocked[index],
          });
        });

        const phase2ZepochNodeRecords = phase2ZepochNodeIds.map((tokenId) => ({
          tokenId,
          phase: 2,
          nftType: NFT_TYPE.ZEPOCH,
        }));

        const zepochNodeRecords = phase1ZepochNodeRecords
          .concat(phase2ZepochNodeRecords)
          .map((item) =>
            convertedStakedZepochNodeIds.includes(item.tokenId)
              ? { ...item, staked: true }
              : { ...item, staked: false },
          )
          .sort((a, b) => a.tokenId - b.tokenId);

        const themisGavelRecords = themisGavelIds.map((tokenId) => ({
          tokenId: tokenId.toNumber(),
          nftType: NFT_TYPE.THEMIS,
        }));

        const idleZepochNode = zepochNodeRecords.filter((item) => !item.staked);
        const stakedZepochNode = zepochNodeRecords.filter((item) => item.staked);
        const unlockableZepochNode = zepochNodeRecords.filter(
          (item) => item?.phase1ZbcUserLocked?.unlockable,
        );

        if (isMounted.current) {
          dispatch({
            type: FETCH_STATE.SUCCESS,
            payload: {
              zepochNodeRecords,
              themisGavelRecords,
              idleZepochNode,
              stakedZepochNode,
              unlockableZepochNode,
            },
          });
        }
      } catch (error) {
        console.log(error);
        if (isMounted.current) {
          dispatch({ type: FETCH_STATE.FAILURE });
        }
      }
    };
    fetch();
  }, [
    managerContract,
    zepochNodeContract,
    themisGavelContract,
    isMounted,
    updateByTimer,
    account,
    managerAddress,
    multicallContract,
  ]);

  return state;
};

